.modal__order {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 5;

  display: none;
  padding: 28px 30px;
  height: 336px;

  background-color: $white;
  border: 1px solid #c6c6c6;
  box-shadow: 0 5px 10px 0 rgba(0, 1, 1, 0.25);

  transform: translate(-50%,-50%);
}

.modal--show {
  display: block;
}

.modal__order-title {
  margin: 0;
  padding: 0 54px;
  width: 150px;

  @include text-size(25px);
  text-align: center;
  text-transform: uppercase;
}

.modal__order-text {
  margin: 0;
  padding: 10px 0 10px 61px;

  @include text-size(15px);
  font-weight: $weight;
  color: $greyish-brown;
}

.modal__order-wrapper {
  display: flex;

  padding-top: 27px;
  padding-bottom: 20px;
}

.modal__order-input:hover + .modal__order-label {
  border: 2px solid rgba(68, 68, 68, 0.3);
}

.modal__order-input:checked + .modal__order-label,
.modal__order-input:active + .modal__order-label {
  border: 2px solid $primary-color;
}

.modal__order-label {
  @include text-size(17px, 16px);
  color: $greyish-brown;
  text-transform: uppercase;

  border: 2px solid rgba(68, 68, 68, 0.1);

  &--s {
    margin-right: 7px;
    padding: 13px 34px;
  }

  &--m {
    margin-right: 7px;
    padding: 13px 30px;
  }

  &--l {
    padding: 13px 33px;
  }
}

.modal__button {
  margin-bottom: 10px;
  padding: 18px 0;

  @include text-size(17px, 16px);
  font-weight: 700;
  color: white;

  background-color: $primary-color;
  border: none;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 4;

  display: none;
  width: 100%;
  height: 100%;

  background-color: rgba(255, 255, 255, 0.9);
}

.overlay--show {
  display: block;
}

@media (min-width: $tablet-width) {
  .modal__order {
    margin: 0 auto;
    padding: 32px 50px;

    width: 554px;

    box-sizing: border-box;
  }

  .modal__order-wrapper {
    padding-left: 56px;
  }

  .modal__order-title {
    padding: 0 0 0 6px;
    width: 100%;

    @include text-size(30px, 57px);
  }

  .modal__order-text {
    padding: 12px 0 9px 150px;

    @include text-size(17px);
  }

  .modal__order-label {
    &--s {
      padding: 13px 48px;
    }

    &--m {
      padding: 13px 44px;
    }

    &--l {
      padding: 13px 48px;
    }
  }

  .modal__button {
    margin-left: 55px;
    width: 350px;
  }
}
